@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: none;

    &__mostrar {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    @include m.tablet {
        display: flex;
        flex-direction: row;
    }

    &__enlace {
        color: v.$blanco;
        font-size: 1.8rem;
        padding: 1rem;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;
        @include m.tablet {
            padding: 1.8rem;
        }

        &--actual,
        &:hover {
            background-color: v.$blanco;
            color: v.$verde;
        }
    }
}