@use 'base/mixins' as m;
@use 'base/variables' as v;

.bloques {
    @include m.contenedor;
    @include m.tablet {
        margin-top: 5rem;
    }

    &__grid {
        @include m.grid(1, 3rem);
        @include m.desktop {
            @include m.grid(2, 4rem);
        }
    }
}

.bloque {
    padding: 3rem;
    color: darken(v.$gris-oscuro, 5);
    border-radius: 1rem;

    &:nth-child(1) {
        @include m.bg-gradient(#dfa384);
    }

    &:nth-child(2) {
        @include m.bg-gradient(#5dc993);
    }

    &:nth-child(3) {
        @include m.bg-gradient(#68f0e7);
    }
    &:nth-child(4) {
        @include m.bg-gradient(#e9eb72);
    }

    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.5rem;
    }

    &__texto {
        margin: 1rem 0;

        &--cantidad {
            margin-top: 5rem;
            font-size: 6rem;
            text-align: center;
            font-weight: v.$black;
        }
    }
}