@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark) {
    $grisOscuro: #202124;
    body {
        background-color: $grisOscuro;
    }

    .registro {
        background-color: $grisOscuro;
        &__heading {
            color: v.$gris-claro;
        }
        &__descripcion {
            color: v.$gris-claro;
        }
    }

    .eventos-registro {
        &__fecha {
            color: v.$gris-claro;
        }
    }
    .registro {
        &__texto {
            color: v.$gris-claro;
        }
        &__label {
            color: v.$gris-claro;
        }
        &__nombre {
            color: v.$gris-claro;
        }
        &__evento {
            &:nth-child(even) {
                background-color: lighten($grisOscuro, 5%);
            }
        }
    }

    .barra {
        background-color:  lighten($grisOscuro, 5%);
    }

    .navegacion {

        &__enlace {

            &:hover {
                color: $grisOscuro;
            }

            &--actual {
                color: $grisOscuro;
            }
        }
    }

    .agenda {

        &__heading {
            color: v.$gris-claro;
        }
        &__descripcion {
            color: v.$gris-claro;
        }
    }

    .eventos {

        &__fecha {
            color: v.$gris-claro;
        }
    }

    .evento {

        &__hora {
            color: v.$gris-claro;
        }
    }
    .speakers {
            
            &__heading {
                color: v.$gris-claro;
            }
            &__descripcion {
                color: v.$gris-claro;
            }
    }
    .speaker {

        &__nombre {
            color: v.$gris-claro;
        }
        &__ubicacion {
            color: v.$gris-claro;
        }
    }

    .speaker-sociales {

        &__enlace {
    
            &::before{
                color: v.$gris-claro;
            }
        }
    }

    .boletos {

        &__heading {
            color: v.$gris-claro;
        }
        &__descripcion {
            color: v.$gris-claro;
        }
    }

    .boleto {

        &::before {
            background-color: $grisOscuro;
        }
        &::after {
            background-color: $grisOscuro;
        }
    }

    .footer {
        background-color: lighten($grisOscuro, 5%) ;

        &__copyright {
            background-color: lighten($grisOscuro, 5%);
        }
    }

    .webtechnology {

        &__heading {
            color: v.$gris-claro;
        }
        &__descripcion {
            color: v.$gris-claro;
        }
        &__contenido {
            color: v.$gris-claro;
        }
    }

    .paquetes {
        &__heading {
            color: v.$gris-claro;
        }
    }

    .paquete {
            
        &__nombre {
            color: v.$gris-claro;
        }
        &__lista {
            color: v.$gris-claro;
        }
    }

    .auth {
        &__heading {
            color: v.$gris-claro;
        }
        &__texto {
            color: v.$gris-claro;
        }
    }
    .formulario {

        &__label {
            color: v.$gris-claro;
        }
    }

    .acciones {
        &__enlace {
            color: v.$gris-claro;
            border-top: 1px solid v.$gris-claro;
            border-bottom: 1px solid v.$gris-claro;

            &:hover {
                color: $grisOscuro;
            }
        }
    }

    .pagina {
        &__heading {
            color: v.$gris-claro;
        }
        &__descripcion {
            color: v.$gris-claro;
        }
    }
}

