@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    background-color: v.$verde;
    &__contenido {
        @include m.contenedor;
        display: flex;
        justify-content: space-between;
        align-items: center
    }

    &__logo {
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
        @include m.tablet {
            display: flex;
        }
    }

    &__logo-desaparecer {
        display: none;
        @include m.tablet {
            display: flex;
        }
    }

    &__mobile-menu {
        @include m.tablet {
            display: none;
        }
        img {
            width: 5rem;
            margin-top: 1rem;
        }

        &:hover {
            cursor: pointer;
        }
    }

    &__izquierda {
        margin-left: 0;

    }

}
