@use 'base/mixins' as m;
@use 'base/variables' as v;

.paginacion {
    margin-top: 1.5rem;
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include m.tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: v.$verde;
        font-size: 1.7rem;
        text-transform: uppercase;

        &--texto {
            padding: .5rem;
            border-radius: .5rem;
            border: 1px solid v.$verde;

            &:hover {
                background-color: v.$verde;
                color: v.$blanco;
            }
        }
        &--numero {
            font-weight: v.$bold;
            display: none;
            @include m.tablet {
                display: inline;
            }
        }

        &--actual {
            color: lighten(v.$gris-oscuro, 30) ;
            /* color: v.$gris; */
            font-weight: v.$bold;
            display: none;
            @include m.tablet {
                display: inline;
            }
        }
    }
}