@use 'base/mixins' as m;
@use 'base/variables' as v;

.table {
    width: 100%;
    border-spacing: 0;

    &__thead {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    }
    &__th {
        padding: 1rem;
        text-align: left;
    }
    &__tr {
        &:nth-child(even) {
            background-color: v.$gris-claro;
        }
    }
    &__td {
        padding: 1.5rem 1rem;

        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 3rem;
            align-items: center;
        }
    }
    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.5rem;
        font-weight: v.$bold;
        &--editar {
            display: flex;
            gap: 1rem;
            padding: 1rem;
            border-radius: 1rem;
            color: v.$blanco;
            background-color: v.$verde;
            &:hover{
                background-color: darken(v.$verde, 7);
            }
        }
        &--eliminar {
            display: flex;
            gap: 1rem;
            padding: 1rem;
            border-radius: 1rem;
            color: v.$blanco;
            background-color: v.$rojo;
            &:hover{
                background-color: darken(v.$rojo, 7);
            }
        }
        &:hover {
            cursor: pointer;
        }
    }
}

table:first-child th:first-child {
    border-top-left-radius: 1rem;
}
table:first-child th:last-child {
    border-top-right-radius: 1rem;
}